
.gallery-outermost-wrapper{
  width: 96vw;
  padding: 1vw;
}
.carousel{
  display: flex;
  flex-direction: row;
  overflow-x: scroll;
}

.carousel::-webkit-scrollbar {
  display: none;
}
.img-wrapper{
  margin: 10px;
  padding: 10px;
}

.img-wrapper button{
  background-color: white;
  border: none;
  outline: none;
  transition: all 0.1s ease-in-out;
}
.img-wrapper button:hover {
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 16px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
  cursor: pointer;
}

.main-view-container{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}